import { Component, OnInit } from "@angular/core";
import { Message } from "primeng/primeng";
import * as _ from 'lodash';
import { Commfunc } from "../../../../../../@uisftech/common/service/commonFun";
import { GreenLoanService } from "../http/GreenLoan.http.service";
import { Router, ActivatedRoute } from "@angular/router";
import { GreenLoanExamBean } from "./bean/green-loan-exam.bean";

@Component({
    selector: 'green-loan-exam',
    templateUrl: './green-loan-exam.component.html',
    styleUrls: ['./green-loan-exam.component.scss'],
    providers: [Commfunc, GreenLoanService]
})
export class GreenLoanExamComponent implements OnInit {
    but: boolean = false;
    code: any;
    detailList: any = [];
    beanExam: any;
    msgs: Message[];//提示
    multipleFlag: any = [];//是否绿色贷款
    taskTypeList: any = [];//绿色贷款分类
    taskTypeListTch: any = [];//绿色贷款分类二级
    noPromptDispaly: boolean = false; // 校验
    waitSelUserList: any;  // 选人
    greenLoanExamBean = new GreenLoanExamBean();
    constructor(
        public commfunc: Commfunc,
        public httpService: GreenLoanService,
        public routeInfo: ActivatedRoute,
    ) {
        //调用码值
        this.codeValues();
        this.multipleFlag = this.code['multipleFlag'];
        // 获取参数
        this.detailList = this.routeInfo.snapshot.params;
        this.greenLoanExamBean = JSON.parse(this.routeInfo.snapshot.params.dataMap);
        this.query();
    }
       // 获取码值
       codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret;
    }
    ngOnInit() {
        this.query();
    }
     //数据查询
     query() {
        this.greenLoanExamBean['statusId'] = '3';
        this.httpService.queryContractEnergySavingLoanApproved(this.greenLoanExamBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.greenLoanExamBean = data;  
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
        })
    }
    //是否绿色贷款
    changeTheme() {
        if (this.greenLoanExamBean.greenLoanFlag == '1') {
            let param = {
                taskTypeKey: 'greenLoanType',
                taskTypeParentId: ""
            }
            this.httpService.taskTypeQry(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    let arr = data.taskTypeList;
                    this.taskTypeList = [{ label: '请选择', value: '' },];
                    arr.forEach(param => {
                        this.taskTypeList.push({ label: param.taskTypeName, value: param.taskTypeId });
                    });
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
            })
        } else {
            this.taskTypeList = '';
        }
    }
    //绿色贷款分类
    changeLoan() {
        if (this.greenLoanExamBean.greenLoanFlag == '1') {
            let param = {
                taskTypeKey: 'greenLoanType',
                taskTypeParentId: ""
            }
            this.httpService.taskTypeQry(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    let arr = data.taskTypeList;
                    this.taskTypeList = [{ label: '请选择', value: '' },];
                    arr.forEach(el => {
                        this.taskTypeList.push({ label: el.taskTypeName, value: el.taskTypeId });
                    });
                    if (this.greenLoanExamBean.greenLoanClassification == '000000') {
                        this.greenLoanExamBean.greenLoanSpecificUse = '';
                        this.but = true;
                    } else {
                        this.but = false;
                    }
                    this.changeLoanA();
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
            })
        } else {
        }
    }
    //绿色贷款分类二级
    changeLoanA() {
        let nexttypeid='';
        if (this.greenLoanExamBean.greenLoanFlag == '1') {
            for(let i = 0; i < this.taskTypeList.length; i++){
                if(this.greenLoanExamBean.greenLoanClassificationName==this.taskTypeList[i].label){
                    nexttypeid=this.taskTypeList[i].value;
                }
            }
            let param = {
                taskTypeKey: 'greenLoanType',
                taskTypeParentId: nexttypeid
            }
            console.info(param, "param122121121")
            this.httpService.taskTypeQry(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    let arr = data.taskTypeList;
                    this.taskTypeListTch = [{ label: '请选择', value: '' },];
                    arr.forEach(param => {
                        this.taskTypeListTch.push({ label: param.taskTypeName, value: param.taskTypeId });
                    });
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
            })
        } else {
        }
    }
}