import { ModuleWithProviders } from "@angular/core";
import { RouterModule, Routes } from "@angular/router";
import { GreenLoanEditComponent } from "./green-loan-edit/green-loan-edit.component";
import { GreenLoanAddComponent } from "./green-loan-add/green-loan-add.component";
import { GreenLoanExamComponent } from "./green-loan-exam/green-loan-exam.component";
import { GreenLoanComponent } from "./credit-green-loan.component";

const routes: Routes = [
    {
        path: '',
        component: GreenLoanComponent,
        children: [
            { path: 'green-loan-add', component: GreenLoanAddComponent },//綠色貸款
            { path: 'green-loan-edit', component: GreenLoanEditComponent },//綠色貸款
            {
                path: 'green-loan-exam',component: GreenLoanExamComponent
            }
        ]
    },
    
];
export const GreenLoanRouting: ModuleWithProviders = RouterModule.forChild(routes);